.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}



.makeup-control {
  background: rgb(16 2 2 / 50%);
  padding: 5px;
  border-radius: 0;
}

.makeup-control h4 {
  color: #ffffff;
  margin: 10px;
}

.control-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
}

.control-body {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

input[type="color"] {
  width: 100%;
  height: 30px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

input[type="range"] {
  width: 100%;
}

input[type="checkbox"] {
  width: 18px;
  height: 18px;
  cursor: pointer;
}

label {
  font-size: 14px;
  font-weight: 500;
}



header {
  background-color: #ff69b4;
  padding: 20px;
  color: white;
}

h1 {
  margin: 0;
}

.product-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
  padding: 20px;
}

.product-card {
  background-color: white;
  border: 1px solid #e0e0e0;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s ease-in-out;
}

.product-card:hover {
  transform: translateY(-5px);
}

.product-img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.product-card h3,
.product-card p {
  margin: 5px 0;
  text-align: center;
}

.tryon-btn {
  background-color: #ff69b4;
  color: white;
  padding: 10px 20px;
  border: none;
  cursor: pointer;
  border-radius: 0 0 10px 10px;
  width: 100%;
}

.tryon-btn:hover {
  background-color: #ff1493;
}

.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
}

.camera {
  width: 80%;
  height: 80%;
  background-color: black;
}

.close-btn {
  position: absolute;
  top: 20px;
  right: 20px;
  background-color: white;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 16px;
  border-radius: 5px;
}

.feature-section {
  position: absolute;
  top: 6%;
  left: 1%;
  height: 100%;
  overflow-y: auto;
}

.color-options {
  display: flex;
  gap: 10px;
  margin-top: 20px;
  justify-content: center;
}

.color-swatch {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  cursor: pointer;
  border: 2px solid transparent;
  transition: border 0.3s;
}

.color-swatch:hover {
  border: 2px solid #fff;
  /* Highlight on hover */
}

.close-btn {
  position: fixed;
  top: 25px;
  right: 10px;
  z-index: 1;
  padding: 8px;
  font-size: 16px;
  border-radius: 4px;
  border: 1px solid #ddd;
  background-color: #ff69b4;
  color: #ffffff;
}

.show-all-feature {
  position: fixed;
  top: 60px;
  right: 10px;
  z-index: 1;
  padding: 8px;
  font-size: 16px;
  border-radius: 4px;
  border: 1px solid #ddd;
  background-color: #ff69b4;
  color: #ffffff;

}

/* Loader spinner */
.loader {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.8);
  /* Transparent background */
  z-index: 9999;
  /* Ensure it's on top of everything */
}

.spinner {
  border: 12px solid #f3f3f3;
  /* Light grey */
  border-top: 12px solid #3498db;
  /* Blue */
  border-radius: 50%;
  width: 80px;
  height: 80px;
  animation: spin 2s linear infinite;
}

/* Animation for the spinner */
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}


footer {
  background-color: #222;
  color: white;
  padding: 20px 0;
  margin-top: 20px;
  position: relative;
  width: 100%;
  left: 0;
  overflow-x: hidden;
  bottom: 0;
}

footer p {
  margin: 0;
}

@media (max-width: 768px) {
  .product-card {
    margin-bottom: 20px;
  }

  .popup .camera {
    width: 95%;
    height: 60%;
  }
}